import express from "express";
import hospitalController from "../Controllers/hospitalController";
import DataChecker from "../Middlewares/dataChecker";
import verifyAccessh from "../Middlewares/verifyAccessh";
import Validator from "../Middlewares/validator";

const hospitalRouter=express.Router();

hospitalRouter.post("/register",
Validator.newHospitalRules(),
Validator.validateInput,
DataChecker.validateEmailDuplication,
hospitalController.registerHospital);
hospitalRouter.get("/signinHospital",hospitalController.signinHospital);
hospitalRouter.get("/allh",verifyAccessh("user","admin"), hospitalController.getAllHospitals);
hospitalRouter.get("/:id",verifyAccessh("user","admin"), hospitalController.getOneHospital);
hospitalRouter.patch("/:id",verifyAccessh("admin"), hospitalController.updateOneHospital);
hospitalRouter.delete("/:id",verifyAccessh("admin"),hospitalController.deleteOneHospital);

export default hospitalRouter;